@import "./../base.scss";
@import "./../grid.scss";

.puskBlock {
  width: 260px;
  height: 311px;
  background-image: url("/img/pusk-block.png");
  @include back-img(contain, center, no-repeat);
  position: fixed;
  z-index: 9;
  left: 2px;
  bottom: 28px;

  .title {
    @include font(16px, 1, $color-white, 400, 2px);
    @include transform(rotate(-90deg));
    position: absolute;
    left: -135px;
    bottom: 148px;
    width: 300px;
    height: 22px;
    display: block;
  }
}